import React, { Component } from 'react';
import { Card, Row, Form, Col, Input, Table, Button } from 'antd';

const dataSource = [
  {
    giaovien: 'VT',
    phong: '',
    monhoc: 'Anh văn 1',
    ca: 'SANG',
  },
  {
    giaovien: 'TA',
    phong: '',
    monhoc: 'Giải tích 1',
    ca: 'SANG',
  },
];

const columns = [
  {
    title: 'Giáo viên',
    dataIndex: 'giaovien',
    key: 'giaovien',
  },
  {
    title: 'Phòng',
    dataIndex: 'phong',
    key: 'phong',
  },
  {
    title: 'Môn học',
    dataIndex: 'monhoc',
    key: 'monhoc',
  },
  {
    title: 'Ca',
    dataIndex: 'ca',
    key: 'ca',
  },
];

  <Table dataSource={dataSource} columns={columns} />;
export default class TableClasses extends Component {
  render() {
    return <Table size="small" dataSource={dataSource} columns={columns} />;
  }
}



